html {
  box-sizing: border-box;
  // scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: $primary-font;
  font-weight: $regular-font;
  font-size: 18px;
  color: $base-color;
  background-color: $background-color;
  margin: 0;
  min-height: 100vh;

  display: grid;
  grid-template-rows: auto 1fr auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

picture {
  display: block;
}
