// 	flex-direction: column;
// 	padding: 30px;
// 	background-color: $button-color;
// }

.card__item {
	display: grid;
	justify-items: center;
	padding: 30px;
	background-color: $button-color;
}

.card__title {
	@include h3;
}

.card__picture {
	grid-row-start: 1;
}

.card__price {
	margin: 0 0 20px 0;
	color: $base-color;
}

.card__link {
	@include button;

	&:hover {
		color: #fff;
		background-color: #3a2026;
	}

	&:active,
	&:focus {
		background-color: #3a2026;
	}
}